@media ( min-width: 321px ) {

    /* SIDEBAR RESPONSIVE */

    .biography-wrapper {
        overflow-y: scroll;
        scrollbar-width: 0.6em;
        scrollbar-color: var(--scroll-bar-color) var(--scroll-bar-background-color);
    }

    ::-webkit-scrollbar {
      width: 0.6em;
    }
    
    ::-webkit-scrollbar-track {
      background: var(--scroll-bar-background-color); /* color of the tracking area */
    }
    
    ::-webkit-scrollbar-thumb {
      background-color: var(--scroll-bar-color); /* color of the scroll thumb */
      border-radius: 0.2em; /* roundness of the scroll thumb */
    }

    /* ABOUT RESPONSIVE */
    .speech-ballon-icon i {
        font-size: 20em;
    }

    .about-text-content {
        width: 20em;
    }

    .speech-ballon-icon {
        top: 40%; left: 20%;
    }
    
    .about-text-content {
        top: 40%; left: 20%;
        padding: 1em 1em 1em 1em;
    }

    .about-text-content h1 {
        font-size: 1.4em;
    }
    
    .about-text-content p {
        font-size: 1em;
    }
    

    /* PORTFOLIO CARDS RESPONSIVE */

    .portfolio-content {
        width: calc(100vw - var(--sidebar-width));
        margin: 0 0 0 auto;
    }

    .portfolio-content header h1 {
        font-size: 1.8em;
    }

    .sidebar-content {
        left: calc(var(--sidebar-content-position-left) + var(--sidebar-width));
    }
    
    .sidebar-wrapper {
        width: calc(var(--sidebar-wrapper-width) - var(--sidebar-width));
    }

    .portfolio-nav {
        display: none;
    }
}

@media ( min-width: 480px ) {
    /* ABOUT RESPONSIVE */

    .speech-ballon-icon i {
        font-size: 26em;
    }

    .speech-ballon-icon {
        top: 30%; left: 15%;
    }
    
    .about-text-content {
        width: 26em;
        top: 30%; left: 15%;
        padding: 2em 3em 1em 3em;
    }
    
    .about-text-content h1 {
        font-size: 1.8em;
    }
    
    .about-text-content p {
        font-size: 1.1em;
    }


    /* PORTFOLIO RESPONSIVE */
    .portfolio-content {
        width: 24em;
        margin: 0 auto;
    }

    .portfolio-nav {
        display: block;
        top: 50%; left: 88%;
        transform: translate(0, -50%);
    }
}

@media ( min-width: 580px ) {

    /* PORTFOLIO RESPONSIVE */
    .portfolio-content {
        width: 30em;
        margin: 0 auto;
    }


    /* SIDEBAR RESPONSIVE */
    .sidebar-content {
        left: -26em;
    }
    
    .sidebar-wrapper {
        width: 26em;
    }
}

@media ( min-width: 768px ) {


    /* ABOUT RESPONSIVE */

    .speech-ballon-icon i {
        font-size: var(--font-size-icon-ballon);
    }
    
    .about-text-content {
        width: var(--font-size-icon-ballon);
    }

    .about-text-content h1{
        font-size: 2.4em;
    }
    
    .about-text-content p {
        font-size: 1.3em;
    }

     /* ABOUT RESPONSIVE */
     .speech-ballon-icon {
        top: 30%; left: 25%;
    }
    
    .about-text-content {
        top: 30%; left: 25%;
    }

    /* PORTFOLIO CARDS RESPONSIVE */

    .portfolio-content {
        width: 38em;
        margin: 0 auto;
    }

    .portfolio-wrapper {
        padding: 0;
    }

    .portfolio-card-wrapper {
        display: flex;
        flex-wrap: wrap;
        /* padding: 0 6em; */
    }


    .portfolio-content header h1 {
        font-size: 3em;
    }

    .portfolio-text-content {
        padding: 2em 0;
    }
   
}

@media ( min-width: 960px ) {
    
    /* PORTFOLIO CARDS RESPONSIVE */
   
    .portfolio-content {
        width: 44em;
        margin: 0 auto;
    }

    .portfolio-content header h1 {
        font-size: 4em;
        margin-left: 0;
    }
}

@media ( min-width: 1025px ) {

    /* ABOUT RESPONSIVE */
    .speech-ballon-icon {
        top: 42%; left: 54%;
    }
    
    .about-text-content {
        top: 42%; left: 54%;
    }
    /* PORTFOLIO CARDS RESPONSIVE */
    .portfolio-wrapper {
        padding: 0 2em 3em 4em;
    }

    .portfolio-content {
        width: 70em;
        margin: 0 auto;
    }

    .portfolio-card-wrapper {
        display: flex;
        flex-wrap: nowrap;
        padding: 0;
    }

    .portfolio-card-wrapper:nth-child(even) .portfolio-media-background {
        order: 1;
    }

    .portfolio-text-content {
        padding: 2em 2em;
    }

    .portfolio-content header h1 {
        margin-left: 50%;
    }
}

@media ( min-width: 1200px ) {

    /* PORTFOLIO CARDS RESPONSIVE */

    .portfolio-content {
        width: 80em;
        margin: 0 auto;
    }
}

body {
    background-color: black
}

