    max-width: var(--main-layout-width);
    background-image: url("./images/Untitled-7.jpg");
    background-repeat: repeat;
    background-size: 100%;
    background-attachment: fixed;
}

.app-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
